import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-cbbo-side-bar',
  templateUrl: './cbbo-side-bar.component.html',
  styleUrls: ['./cbbo-side-bar.component.css']
})
export class CbboSideBarComponent implements OnInit {
  name:any

  constructor(private appServ:AppService) { }

  ngOnInit(): void {
    this.fetchCbboName()
  }

  
  fetchCbboName(){
    let data={
      type:'Cbbo',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data=>{
        this.name=data.name
      }
    )
  }
}
