import { Component, OnInit } from '@angular/core';
import { RootAdminService } from '../root-admin.service';

@Component({
  selector: 'app-root',
  templateUrl: './root.component.html',
  styleUrls: ['./root.component.css']
})
export class RootComponent implements OnInit {
  width:any
  pageWidth:any
  constructor(private adminService:RootAdminService) { }

  ngOnInit(): void {
    this.adminService.sideBarOpen.subscribe(
      data=>{
       if(data===true){
         this.width=0
         this.pageWidth=100
       }else{
         this.width=200
         this.pageWidth=84.5
       }
      }
    )
  }
}
