import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';
import { RootComponent } from './adminLayout/admin/root/root.component';
import { AuthGuardGuard } from './auth-layout/authGuard/auth-guard.guard';
import { LoginComponent } from './auth-layout/login/login.component';
import { MainComponent } from './cbbo-layout/cbbo/main/main.component';
import { MaincomponentComponent } from './ia-layout/ia/maincomponent/maincomponent.component';
import { ConsumerGroupComponent } from './consumerGroup/consumer-group/consumer-group.component';
import { RootLayoutComponent } from './fig-layout/root-layout/root-layout.component';
import { AdminLayoutComponent } from './layout/admin-layout/admin-layout.component';
import { RootcomponentComponent } from './adminmaster/readmin/rootcomponent/rootcomponent.component';
const routes: Routes = [
  {
    path: 'fpo',
    component:AdminLayoutComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['FPO']},
    children:[{
      path:'',
      loadChildren: 
      // './layout/admin-layout/admin-layout.module#AdminLayoutModule'
      ()=>import ('./layout/admin-layout/admin-layout.module').then(module=>module.AdminLayoutModule)
    }]
  },
  {
    path:"userLogin",
    redirectTo:'login',
    pathMatch:'full'
  },
  {
    path:"",
    redirectTo:'login',
    pathMatch:'full'
  },
  {
    path:"admin",
    component:RootComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['Admin']},
    children:[{
      path:"",
      loadChildren:
      // './adminLayout/admin/admin.module#AdminModule'
      ()=>import ('./adminLayout/admin/admin.module').then(module=>module.AdminModule)
    }]
  },
  {
    path:"ReAdmin",
    component:RootcomponentComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['ReAdmin']},
    children:[{
      path:"",
      loadChildren:
      // './adminmaster/readmin/readmin.module#ReadminModule'
      ()=>import ('./adminmaster/readmin/readmin.module').then(module=>module.ReadminModule)
    }]
  },
  {
    path:"cbbo",
    component:MainComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['Cbbo']},
    children:[{
      path:"",
      loadChildren:
      // './cbbo-layout/cbbo/cbbo.module#CbboModule'
      ()=>import ('./cbbo-layout/cbbo/cbbo.module').then(module=>module.CbboModule)
    }]
  },
  {
    path:"ia",
    component:MaincomponentComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['ia']},
    children:[{
      path:"",
      loadChildren:
      // './ia-layout/ia/ia.module#IaModule'
      ()=>import ('./ia-layout/ia/ia.module').then(module=>module.IaModule)

    }]
  },
  {
    path:'login',
    component:LoginComponent
  },
  {
    path:'fig',
    component:RootLayoutComponent,
    canActivate:[AuthGuardGuard],
    data:{role:['FIG']},
    children:[{
      path:'',
      loadChildren:
      // './fig-layout/fig-layout.module#FigLayoutModule'
      ()=>import ('./fig-layout/fig-layout.module').then(module=>module.FigLayoutModule)
    }]
  },
  {
    path:'consumerGroup',
    component:ConsumerGroupComponent,
    canActivate:[AuthGuardGuard],
    data:{ role: ['ConsumerGroup','Trader'] },
    children:[{
      path:'',
      loadChildren:
      // './consumerGroup/consumer-group/consumer-group.module#ConsumerGroupModule'
      ()=>import ('./consumerGroup/consumer-group/consumer-group.module').then(module=>module.ConsumerGroupModule)
    }]
  }
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes,{
      //onSameUrlNavigation:'reload',
      useHash:true
    })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
