import { BreakpointObserver } from '@angular/cdk/layout';
import { Component, OnInit } from '@angular/core';
import { ConsumerGroupService } from './consumer-group.service';

@Component({
  selector: 'app-consumer-group',
  templateUrl: './consumer-group.component.html',
  styleUrls: ['./consumer-group.component.css']
})
export class ConsumerGroupComponent implements OnInit {

  width:any
  pageWidth:any

  constructor(private cgServ:ConsumerGroupService,private observer:BreakpointObserver) { }

  ngOnInit(): void {
    this.cgServ.sideBarOpen.subscribe(
      data=>{
       if(data===true){
         this.width=0
         this.pageWidth=100
       }else{
         this.width=200
         this.pageWidth=84.5
       }
      }
    )
  }

}
