import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ReadminService } from '../readmin.service';
import { AppService } from 'src/app/app.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-mappingmis',
  templateUrl: './mappingmis.component.html',
  styleUrls: ['./mappingmis.component.css']
})
export class MappingmisComponent implements OnInit {
  getIaData: any;
  getCbboData: any;
  getFpoData: any;
  assignData: any;
  IaCbboFpodata: any;

  constructor(private toastr: ToastrService, private router: Router,private appServ:AppService,private service:ReadminService) { }

  ngOnInit(): void {
    this.getIa();
    this.getCbbo();
    this.getFpo();
    this.getIaCbboFpo()
  }
  mappForm = new FormGroup({
    iaName: new FormControl('', [Validators.required]),
    cbboName: new FormControl('', [Validators.required]),
    year: new FormControl('', [Validators.required]),
    fpoName: new FormControl('', [Validators.required]),
  })

  getIa() {
    this.service.getIa().subscribe(result => {
      this.getIaData = result;
    })
  }
  getCbbo() {
    this.service.getCbbo().subscribe(result => {
      this.getCbboData = result;
    })
  }
  getFpo() {
    this.service.getFpo().subscribe(result => {
      this.getFpoData = result;
    })
  }

  assign(){
    const data = {
    iaName:this.mappForm.value.iaName.iaName,
    cbboName:this.mappForm.value.cbboName.cbboName,
    year:this.mappForm.value.year,
    fpoName:this.mappForm.value.fpoName.fpoName,
    }
    this.service.assign(data).subscribe(result => {
      this.assignData=result;
      alert("Details Assigned")
    this.toastr.success('Data Assigned successfully')
    this.mappForm.reset();
    this.getIaCbboFpo()
    })
  }

  getIaCbboFpo(){
    this.service.getIaCbboFpo().subscribe(result =>{
      this.IaCbboFpodata=result;
    })
  }
}
