import { Component, OnInit } from '@angular/core';
import { FigServiceService } from '../fig-service.service';

@Component({
  selector: 'app-root-layout',
  templateUrl: './root-layout.component.html',
  styleUrls: ['./root-layout.component.css']
})
export class RootLayoutComponent implements OnInit {
  width:any
  pageWidth:any
  constructor(private figServ:FigServiceService) { }

  ngOnInit(): void {
    this.figServ.sideBarOpen.subscribe(
      data=>{
       if(data===true){
         this.width=0
         this.pageWidth=100
       }else{
         this.width=200
         this.pageWidth=84.5
       }
      }
    )
  }

}
