import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-iasidebar',
  templateUrl: './iasidebar.component.html',
  styleUrls: ['./iasidebar.component.css']
})
export class IasidebarComponent implements OnInit {

  constructor(private appServ:AppService) { }

  ngOnInit(): void {
    this.fetchIaName()
  }
  name:any
  fetchIaName(){
    let data={
      type:'ia',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data=>{
        this.name=data.name
      }
    )
  }

  open_dashboard(){
    // window.open('../../../../../../assets/newDesign/index1.html', '_blank')
    window.open('https://fpoodisha.nic.in/dashboardia', '_blank')
  }

  open_dashboardCbbo(){
    // window.open('../../../../../../assets/newDesign/index1.html', '_blank')
    window.open('https://fpoodisha.nic.in/dashboardCbbo', '_blank')
  }
}
