import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-cg-sidebar',
  templateUrl: './cg-sidebar.component.html',
  styleUrls: ['./cg-sidebar.component.css']
})
export class CgSidebarComponent implements OnInit {

  constructor(private appServ:AppService,private router:Router) { }

  ngOnInit(): void {
    this.btnClick();
    this.fetchCgName()

  }

  navActive: any;
  subNavActive: any;

  name:any
  userType:any


  fetchCgName(){
    let data={
      type:'ConsumerGroup',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data=>{
        if(!data.name){
          // this.router.navigate(['/login'])
        }
        this.name=data.name
        this.userType=data.type
      }
    )
  }

  btnClick() {
    $(this).toggleClass('click');
    $('.sidebar').toggleClass('show');
  }

  featBtn() {
    $('nav ul .feat-show').toggleClass('show');
    $('nav ul .first').toggleClass('rotate');
  }

  servBtn() {
    $('nav ul .serv-show').toggleClass('show1');
    $('nav ul .second').toggleClass('rotate');
  }
  servBtn1() {
    $('nav ul .serv-show2').toggleClass('show2');
    $('nav ul .third').toggleClass('rotate');
  }

  liActive() {
    // this.navActive=num
    $(this).addClass('active').siblings().removeClass('active');
  }
  subLiActive() {
    // this.subNavActive=num
  }

}
