import { BreakpointObserver } from '@angular/cdk/layout';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AppService } from 'src/app/app.service';
import { AuthService } from 'src/app/auth-layout/auth.service';
import { ChangePasswordComponent } from 'src/app/auth-layout/change-password/change-password.component';
import { FigServiceService } from '../../fig-service.service';

@Component({
  selector: 'app-fig-topbar',
  templateUrl: './fig-topbar.component.html',
  styleUrls: ['./fig-topbar.component.css']
})
export class FigTopbarComponent implements OnInit {
  name:any
  open=true
  buttonPlacement:any
  constructor(private figServ:FigServiceService,private observer:BreakpointObserver,private dialog: MatDialog,private authServ:AuthService,private router:Router,private appServ:AppService) { }

  ngOnInit(): void {
    this.fetchFpoName()
    this.observer.observe(['(max-width:800px']).subscribe(
      res=>{
        if(res.matches){
          this.buttonPlacement='right'
        }else{
        this.buttonPlacement='left'
        }
      }
    )
  }

  navToggle(){
    this.open=!this.open
    this.figServ.sideBarOpen.next(this.open)
  }

  fetchFpoName(){
    let data={
      type:'FIG',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data=>{
        if(!data.name){
          this.router.navigate(['/login'])
        }
        this.name=data.name
      }
    )
  }

  logout(){
    this.authServ.logout().subscribe(
      data=>{
        if(data.status==true){
          this.router.navigate(['/login'])
        }
      }
    )
  }

  openDialogForPasswordChange() {

    const dialogConfig = new MatDialogConfig();
    // dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    // dialogConfig.width='300px'
    // dialogConfig.height='420px'
    dialogConfig.data={
      id:this.appServ.figRefNo,
      type:'FIG',
    }
    this.dialog.open(ChangePasswordComponent,dialogConfig).afterClosed().subscribe(
      ()=>{
        // this.approvedProduced()
      }
    );
  }


}
