import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuardGuard } from 'src/app/auth-layout/authGuard/auth-guard.guard';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MappingformComponent } from './mappingform/mappingform.component';
import { MappingmisComponent } from './mappingmis/mappingmis.component';
import { ApprovefpoComponent } from './approvefpo/approvefpo.component';
const routes: Routes = [
  {
    path: '',
    redirectTo:'dashboard',
    pathMatch:'full',
  },
  {
    path:'dashboard',
    canActivate:[AuthGuardGuard],
    data:{role:['ReAdmin']},
    component:DashboardComponent
  },
  {
    path:'ia-cbbo-mapping',
    canActivate:[AuthGuardGuard],
    data:{role:['ReAdmin']},
    component:MappingformComponent
  },
  {
    path:'ia-cbbo-fpo-mapping',
    canActivate:[AuthGuardGuard],
    data:{role:['ReAdmin']},
    component:MappingmisComponent
  },
  {
    path:'approvefpo',
    canActivate:[AuthGuardGuard],
    data:{role:['ReAdmin']},
    component:ApprovefpoComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReadminRoutingModule { }
