import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';

@Component({
  selector: 'app-fig-sidebar',
  templateUrl: './fig-sidebar.component.html',
  styleUrls: ['./fig-sidebar.component.css'],
})
export class FigSidebarComponent implements OnInit {
  constructor(private appServ:AppService) {}

  ngOnInit(): void {
    this.btnClick();
    this.fetchFigName()
  }

  navActive: any;
  subNavActive: any;

  name:any

  fetchFigName(){
    let data={
      type:'FIG',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data=>{        
        this.name=data.name
      }
    )
  }

  btnClick() {
    $(this).toggleClass('click');
    $('.sidebar').toggleClass('show');
  }

  featBtn() {
    $('nav ul .feat-show').toggleClass('show');
    $('nav ul .first').toggleClass('rotate');
  }

  servBtn() {
    $('nav ul .serv-show').toggleClass('show1');
    $('nav ul .second').toggleClass('rotate');
  }
  servBtn1() {
    $('nav ul .serv-show2').toggleClass('show2');
    $('nav ul .third').toggleClass('rotate');
  }

  liActive() {
    // this.navActive=num
    $(this).addClass('active').siblings().removeClass('active');
  }
  subLiActive() {
    // this.subNavActive=num
  }
}
