import { BreakpointObserver } from '@angular/cdk/layout';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { AppService } from 'src/app/app.service';
import { AuthService } from 'src/app/auth-layout/auth.service';
import { ChangePasswordComponent } from 'src/app/auth-layout/change-password/change-password.component';
import { AdminService } from '../../admin-layout/admin.service';
import { ForcefullyFpoProfileUpdateDialogComponent } from 'src/app/layout/forcefully-fpo-profile-update-dialog/forcefully-fpo-profile-update-dialog.component'

@Component({
  selector: 'app-top-bar',
  templateUrl: './top-bar.component.html',
  styleUrls: ['./top-bar.component.css']
})
export class TopBarComponent implements OnInit {

  name: any
  open = true
  buttonPlacement: any
  PasswordChangedOrNot: any

  constructor(private fpoServ: AdminService, private observer: BreakpointObserver, private dialog: MatDialog, private route: Router, private authServ: AuthService, private router: Router, private appServ: AppService) { }

  ngOnInit(): void {
    this.fetchFpoName()
    this.checkForPasswordChangedOrNot()

    this.observer.observe(['(max-width:800px']).subscribe(
      res => {
        if (res.matches) {
          this.buttonPlacement = 'right'
        } else {
          this.buttonPlacement = 'left'
        }
      }
    )
  }

  checkForPasswordChangedOrNot() {
    this.fpoServ.checkForPasswordChangedOrNot(this.appServ.fpoId).subscribe(
      data => {
        if (data.changed == true) {
          this.PasswordChangedOrNot = true
          // this.openDialogForFpoProfileUpdate()

        } else {
          this.PasswordChangedOrNot = false
          this.openDialogForPasswordChange()
        }
      }
    )
  }

  navToggle() {
    this.open = !this.open
    this.fpoServ.sideBarOpen.next(this.open)
  }


  fetchFpoName() {
    let data = {
      type: 'FPO',
    }
    this.appServ.sideBarUserName(data).subscribe(
      data => {
        if (!data.name) {
          this.route.navigate(['/login'])
        }
        this.name = data.name
      }
    )
  }

  logout() {
    this.authServ.logout().subscribe(
      data => {
        if (data.status == true) {
          this.router.navigate(['/login'])
        }
      }
    )
  }

  openDialogForPasswordChange() {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = !this.PasswordChangedOrNot;
    dialogConfig.autoFocus = true;
    // dialogConfig.width='300px'
    // dialogConfig.height='420px'
    dialogConfig.data = {
      id: this.appServ.fpoRefNo,
      type: 'FPO',
      passwordChangedOrNot: this.PasswordChangedOrNot
    }

    this.dialog.open(ChangePasswordComponent, dialogConfig).afterClosed().subscribe(
      () => {
        // this.approvedProduced()
        this.checkForPasswordChangedOrNot()
      }
    );
  }

  openDialogForFpoProfileUpdate() {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '90%'
    dialogConfig.height = '90%'
    // dialogConfig.data={
    //   id:this.appServ.fpoRefNo,
    //   type:'FPO',
    //   passwordChangedOrNot:this.PasswordChangedOrNot
    // }

    this.dialog.open(ForcefullyFpoProfileUpdateDialogComponent, dialogConfig).afterClosed().subscribe(
      () => {
        // this.approvedProduced()
      }
    );
  }

}
