import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ReadminService {

  constructor(private http: HttpClient) { }

  getIa(): Observable<any> {
    return this.http.get(`${environment.port}/readminUpdate/getIa`);
  }

  getCbbo(): Observable<any> {
    return this.http.get(`${environment.port}/readminUpdate/getCbbo`);
  }

  submit(data:any):Observable<any>{
    return this.http.post(`${environment.port}/readminUpdate/submit`, data);
  }

  getIaCbbo() :Observable<any>{
    return this.http.get(`${environment.port}/readminUpdate/getIaCbbo`);
  }

  getFpo() :Observable<any>{
    return this.http.get(`${environment.port}/readminUpdate/getFpo`);
  }

  assign(data:any):Observable<any>{
    return this.http.post(`${environment.port}/readminUpdate/assign`, data);
  }

  getIaCbboFpo() :Observable<any>{
    return this.http.get(`${environment.port}/readminUpdate/getIaCbboFpo`);
  }
  getCbboMaster(iaName:any):Observable<any>{
    console.log(iaName,'iaName');
    return this.http.get(`${environment.port}/readminUpdate/getCbboMaster/`+ iaName);
  }
}
