  getAverage() {
    const FinYear = this.targetForm.value.finyear
    this.service.getAverage(FinYear).subscribe(result => {
      this.avgData = result;
      this.targetForm.patchValue({
        presentstatusofAvgshareholdermobilized: this.avgData[0]?.AverageValue
      })
    })
  }
